"use client"

import { useState, useEffect, Suspense } from "react"
import Link from "next/link"
import { Navigation } from "@/components/navigation"
import { Deck } from "@/lib/def" // Pake tipe data dari def
import { Loader2, Trophy, Flame } from "lucide-react"

function DecksContent() {
  const [decks, setDecks] = useState<Deck[]>([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const fetchDecks = async () => {
      try {
        const res = await fetch('/api/decks')
        if (res.ok) {
          const data = await res.json()
          setDecks(data)
        }
      } catch (error) {
        console.error("Error fetching decks:", error)
      } finally {
        setIsLoading(false)
      }
    }
    fetchDecks()
  }, [])

  if (isLoading) {
    return (
      <main className="min-h-screen bg-background pb-24 pt-10 flex items-center justify-center">
        <div className="flex flex-col items-center gap-4">
            <Loader2 className="w-10 h-10 animate-spin text-primary" />
            <p className="text-muted-foreground">Loading Meta Decks...</p>
        </div>
        <Navigation />
      </main>
    )
  }

  return (
    <main className="min-h-screen bg-background pb-24 pt-10">
      <div className="max-w-5xl mx-auto p-4 space-y-6">
        <div className="flex items-center justify-between pb-4 border-b border-white/10">
          <h1 className="text-2xl font-bold">Meta Decks</h1>
          <span className="text-sm text-muted-foreground bg-secondary/50 px-3 py-1 rounded-full">
            {decks.length} Decks
          </span>
        </div>

        <div className="grid gap-4">
          {decks.map((deck) => (
            <Link key={deck.id} href={`/decks/${deck.id}`}>
              <div className="bg-card border border-border p-4 rounded-xl hover:border-primary transition-all cursor-pointer flex items-center justify-between group">
                <div className="space-y-1">
                  <div className="flex items-center gap-2">
                    <h3 className="font-bold text-lg group-hover:text-primary transition-colors">{deck.name}</h3>
                    <span className={`text-xs px-2 py-0.5 rounded font-bold ${
                      deck.tier === "S" ? "bg-red-900/50 text-red-400 border border-red-800" :
                      deck.tier === "A" ? "bg-orange-900/50 text-orange-400 border border-orange-800" :
                      "bg-blue-900/50 text-blue-400 border border-blue-800"
                    }`}>
                      Tier {deck.tier}
                    </span>
                  </div>
                  <p className="text-sm text-muted-foreground line-clamp-1">{deck.description}</p>
                </div>
                
                <div className="flex items-center gap-4 text-sm font-medium">
                    <div className="flex flex-col items-end text-muted-foreground">
                        <span className="flex items-center gap-1 text-yellow-500">
                             <Trophy className="w-3 h-3" /> Win Rate {deck.winRate}%
                        </span>
                        <span className="flex items-center gap-1 text-orange-500">
                             <Flame className="w-3 h-3" /> Usage {deck.usage}%
                        </span>
                    </div>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>
      <Navigation />
    </main>
  )
}

export default function DecksPage() {
  return (
    <Suspense fallback={<div>Loading...</div>}>
      <DecksContent />
    </Suspense>
  )
}